            content: `When invited at a party in a big German city, showing up at the precise time will make you look very provincial, just as it would in Paris or London. Conversely, being 10 minutes late for a business meeting will be frowned upon.</p>

<p>While German time-keeping has evolved in line with global norms, there are signs that they never were any more punctual than other European nations. The concept of academic quarter (<em>akademische Viertelstunde</em>, a tongue-in-cheek way of saying that an even will not start on time) exists at least <a href="https://books.google.com/ngrams/graph?content=akademische+Viertelstunde%2Cakademisches+Viertel&year_start=1800&year_end=2008&corpus=20&smoothing=10&share=&direct_url=t1%3B%2Cakademische%20Viertelstunde%3B%2Cc0%3B.t1%3B%2Cakademisches%20Viertel%3B%2Cc0" target="_blank">since the late 19<sup>th</sup> century</a>. `
