            title: "Germans have a word for everything (it's called <em>das Einwortfüralleshaben</em>)",
            content: `The German language makes it easy to collate words together to form new ones, to verb or to substantivate (to noun a verb and vice-versa), like in English. However, until the mid-20th century, German from Germany sounded much more normal to English ears. Radio was <em>Radio</em> and a dentist was <em>ein Dentist</em>, not <em>Rundfunk</em> and <em>Zahnarzt</em> like today. </p>

<p>What happened was <em><a href="https://archive.org/details/sprichdeutschein00engeuoft/page/n3" target="_blank">Entwelschung</a></em> or "de-welshification" of the German language, an understatement that meant "removing Latin words from everyday use". With help from a few nationalist governments, what started like a crusade from a few loonies became official policies and textbooks were dutifully cured of their foreign words. To the point where no one remembers that these strange German composite words are the result of political action, not a linguistic trait.`
