     lede: `Vor zehn Jahren, als ich in Deutschland gelandet bin, dachte ich, dass es ein Ding gab, das alles, was ich hier merkwürdig fand, wie höfliche Polizisten oder Pfandflaschen, erklären könnte. Dieses Ding, das ich Deutschtum nannte, habe ich gesucht, vergeblich. Stattdessen konnte ich festlegen, dass was als deutsche Tugende paradierte entweder nicht existierte oder konnte durch Fakten erklärt werden.</p><p>Kultur ist immer ein Symptom, nie eine Ursache. Deutschtum auch.`,
