            title: "Die Deutschen sind fleißige Arbeiter",
            img: "gdp_de.png",
            content: `Viele Deutschen schwärmen über die Exportweltmeisterschaft, wo Deutschland viel besser als im Männerfußball ist. In der Tat sind viele mittelständische Firmen Weltmeister in ihrer Märkte. Katz GmbH, zum Beispiel, liefert 70% der Bierdeckel der Welt. Sikora ist Nummer Eins für Mess-, Regel- und Sortiergeräte (keine Ahnung, was das ist). Die faule Erklärung wäre, dass Deutsche fleißig und arbeitsam sind. Aber wer schon einmal in Berlin war weißt, wie lächerlich diese Erklärung ist.</p>
            <p>Der Grund, warum deutsche Firmen so gut sind, ist dass, im Gegenteil mit Paris, London oder Madrid, es in Deutschland keinen schwarzen Loch gibt, wo die ganze Energie des Landes fließt und verschwindet. In anderen Ländern muss man wählen zwischen in die Hauptstadt umziehen oder eine zweitklassige Karriere machen. Katz sitzt in Weisenbach, im Schwarzwald, und Sikora in Bremen. Von beide aus braucht man nur eine Autostunde um an einem internationalen Flughafen zu gelangen (Stuttgart und Hamburg). Das hilft, bei Exporten. Aber wichtiger ist, dass die Weisenbacher oder die Bremer sich nicht als Bürger zweiter Klasse fühlen, nur weil sie weit weg von Berlin geboren waren.`
