            content: `Dass die Deutschen für jede Begriffe ein Wort haben ist ein der populärsten Twitter-Mem (es gibt sogar <a href="https://twitter.com/words_german" target="_blank">einen Twitter-Nutzer</a>, der nur solche Wörter twittert). Es war nicht immer so. Bis zum Anfang des 20. Jahrhunderts klang die deutsche Sprache viel ähnlicher zum englischen oder französischen. Der Rundfunk hieß noch <em>Radio</em> und Zahnärzte waren noch <em>Dentisten</em>.</p>
            <p>Es geschah die <em><a href="https://archive.org/details/sprichdeutschein00engeuoft/page/n3" target="_blank">Entwelschung</a></em> (kurz für die Auspflückung fremder Wörter) der Sprache. Es war am Anfang nur Wahnsinn von nationalistischen Spinnern, aber einige Regierungen haben davon eine Leitpolitik gemacht und sorgfältig Schulbücher entlateinisiert. Dass unsere Sprache merkwürdig für Amerikaner und Engländer klingt, hat wenig mit der Sprachwissenschaft zu tun, und viel mehr mit der Politik.`
