            title: "Die Deutschen kümmern sich viel um andere",
            content: `Sie sind im Park, bereits zum Grillen. Es kommt dann einen Typ, der gezielt an Ihre Gruppe geht, nur um Ihnen zu sagen, dass Ihr Grill nach Mindeshöhevorschreibung des Parkanlagengesetzes nicht gerecht sei. Dann geht er wieder weg.</p><p>Nicht wenige Deutschen lieben es, Andere zu sagen, wie sie sich benehmen sollten. Das würde nie in anderen europäischen Großstädten stattfinden, ist aber sehr normal in kleinere Dörfer - und das europaweit. Die kulturelle Erklärung fällt aus.</p><p>Deutsche Großstädte sind anders, weil Leute dort <a href="http://appsso.eurostat.ec.europa.eu/nui/show.do?query=BOOKMARK_DS-051618_QID_70D0A5C9_UID_-3F171EB0&layout=TIME,C,X,0;CITIES,L,Y,0;INDIC_UR,L,Z,0;UNIT,L,Z,1;INDICATORS,C,Z,2;&zSelection=DS-051618INDIC_UR,PS1012V;DS-051618UNIT,PC;DS-051618INDICATORS,OBS_FLAG;&rankName1=INDIC-UR_1_2_-1_2&rankName2=UNIT_1_2_-1_2&rankName3=INDICATORS_1_2_-1_2&rankName4=TIME_1_0_0_0&rankName5=CITIES_1_2_0_1&sortR=ASC_4&sortC=ASC_-1_FIRST&rStp=&cStp=&rDCh=&cDCh=&rDM=true&cDM=true&footnes=false&empty=false&wai=false&time_mode=NONE&time_most_recent=false&lang=EN&cfo=%23%23%23%2C%23%23%23.%23%23%23" target="_blank">sich einander vertrauen</a>. Dieses Vertrauen kommt von der besseren institutionellen Struktur des Landes, die man mit der Tatsache, dass ein deutscher Polizist öfter um Ihnen zu helfen kommt, als um Ihnen Tränengas zu werfen, zusammenfassen kann.</p><p>Die deutsche Polizei, wie die anderen Institutionen auch, rutscht schnell nach mehr Gewalt und mehr Ausgrenzung, und somit das Vertrauen überall vernichtet. Das, prophezeie ich, wird dazu führen, dass keine Typen sich vertrauen werden, anderen in der Straße ihre Meinung zu geben. Sie werden dafür zu viel Angst haben.`
